.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nav{
  
  margin-bottom: 10px;
  /* border: #282c34 2px solid; */
  display: flex;
  padding: 10px;
  align-items:center;
  background-color: #000;
  
}
.nav div{
  display: flex;
  align-items:center;
  background-color: #000;
  cursor: pointer;
}
.nav h1{
  color: azure;
  margin: 8px 0 0 8px;
  font-family: 'Pangolin';
  letter-spacing: 2px;
  font-size: 40px;
}

.nav img{
  height: 60px;

}

.github{
  display: block;
  text-align: end;
}
.github a{
  text-decoration: none;
  font-size: small;
  color: rgb(136, 136, 136);
  
}